import React from 'react'

import '../../assets/css/about.css'
import { Container, Row, Col } from 'react-bootstrap';
import { Reveal, Fade } from 'react-reveal';
import { Link } from "react-router-dom";

export default function index({ mobile }) {
    return (
        <>
            {mobile ? <Reveal left>
                <div className="about-section">
                    <div className="inner-container" style={{ textAlign: 'center' }}>
                        <Reveal top>
                            <h1 > propos du Hack'Prise</h1>

                            <p>
                                Suite au succs incontestable de la 15me dition du Forum Annuel de l'ENSI, lENSI Junior Entreprise est de retour pour organiser la premire dition de son nouvel vnement Hack Prise.
                                Fidle  ses traditions, LENSI Junior Entreprise, ne cesse pas dinnover et vous prsente lopportunit de participer  une comptition hybride qui se droulera le 14 Juillet.
                                L'vnement est reparti en deux hackathons avec deux entreprises de grande renomme et des sujets diversifis qui varient de l'IT  l'entrepreneuriat qui ont un fort impact social et technologique.
                                Accompagn par un soutien technique tout au long de la comptition, vous auriez la chance de gagner des prix exceptionnels.
                                Soyez parmi les crateurs des solutions futures aux problmes de la Tunisie daujourdhui en participant  une exprience aussi enrichissante sur le plan personnel que sur le plan professionnel.                            </p>
                            <div className="btnContainer">

                                <Link to="/about/infor" className="btn-outline" >Hackathon Infor</Link>
                                <Link to="/about/stb" className="btn-outline" >Hackathon STB</Link>
                            </div>

                        </Reveal>
                    </div>

                </div>
            </Reveal> :
                <div style={{ textAlign: 'center', paddingTop: '6%', paddingBottom: '3%' }}>
                    <Container >
                        <Row>
                            <Col className="ml-auto mr-auto" md="8">
                                <Fade big>
                                    <h2 className="title"> propos du Hack'Prise</h2>
                                </Fade>
                                <Reveal bottom>
                                    <p className="description">
                                        Suite au succs incontestable de la 15me dition du Forum Annuel de l'ENSI, lENSI Junior Entreprise est de retour pour organiser la premire dition de son nouvel vnement Hack Prise.
                                        Fidle  ses traditions, LENSI Junior Entreprise, ne cesse pas dinnover et vous prsente lopportunit de participer  une comptition hybride qui se droulera le 14 Juillet.
                                        L'vnement est reparti en deux hackathons avec deux entreprises de grande renomme et des sujets diversifis qui varient de l'IT  l'entrepreneuriat qui ont un fort impact social et technologique.
                                        Accompagn par un soutien technique tout au long de la comptition, vous auriez la chance de gagner des prix exceptionnels.
                                        Soyez parmi les crateurs des solutions futures aux problmes de la Tunisie daujourdhui en participant  une exprience aussi enrichissante sur le plan personnel que sur le plan professionnel.
                                    </p>
                                    <div className="btnContainer">

                                        <Link to="/about/infor" className="btn-outline" >Hackathon Infor</Link>
                                        <Link to="/about/stb" className="btn-outline" >Hackathon STB</Link>
                                    </div>
                                </Reveal>
                                <br />
                            </Col>
                        </Row>
                        <br />
                        <br />
                        <Row>
                            <Col md="3" >

                            </Col>

                            <Col md="3" style={{ padding: '20px' }}>
                                <Reveal left>
                                    <img
                                        width="60%"
                                        alt="..."
                                        src={require("../../assets/img/logo/EJE + Text.png").default}
                                    />
                                </Reveal>
                            </Col>
                        </Row>
                    </Container>
                </div>



            }
        </>
    )
}
